<template>
  <div class="login-container">
    <!-- 导航栏 -->
    <van-nav-bar title="登录" class="page-nav-bar" :border="false">
      <van-icon name="cross" slot="left" @click="$router.back()" />
    </van-nav-bar>
    <!-- 登录表单 -->
    <van-form ref="loginForm" @submit="onSubmit">
      <!-- 表单验证：给wan-field配置rules验证规则，提交时会自动触发，通过触发submint，不通过不触发 -->
      <van-field name="mobile" placeholder="请输入手机号" v-model="user.mobile" :rules="userFormRules.mobile" type="number" maxlength="11">
        <i slot="left-icon" class="toutiao toutiao-shouji"></i>
      </van-field>
      <van-field name="code" placeholder="请输入验证码" v-model="user.code" :rules="userFormRules.code" type="number" maxlength="6">
        <i slot="left-icon" class="toutiao toutiao-yanzhengma"></i>
        <template #button>
          <!-- time倒计时时间 -->
          <van-count-down :time="1000 * 60" format=" ss s" v-if="isCountDownShow" @finish="isCountDownShow = false" class="send-sms-btn" />
          <van-button size="small" round type="default" class="send-sms-btn" @click="onSendSms" native-type="button" v-else>发送验证码</van-button>
        </template>
      </van-field>
      <div style="margin: 16px" class="button-sub">
        <van-button block type="info" native-type="submit" class="login-btn">登录</van-button>
      </div>
    </van-form>
  </div>
</template>

<script>
import { login, sendSms } from '@/api/user'
export default {
  name: 'LoginIndex',
  data() {
    return {
      user: {
        mobile: '',
        code: ''
      },
      // 验证规则
      userFormRules: {
        mobile: [
          { required: true, message: '手机号不能为空' },
          {
            pattern: /1[3|5|7|8|9]\d{9}/,
            message: '手机号格式错误'
          }
        ],
        code: [
          { required: true, message: '验证码不能为空' },
          {
            pattern: /^\d{6}$/,
            message: '验证码格式错误'
          }
        ]
      },
      // 是否展示倒计时
      isCountDownShow: false
    }
  },
  methods: {
    async onSubmit() {
      // 1.获取表单数据
      const user = this.user
      // 2.验证表单
      // 在组件中必须通过this.$toast来调用
      this.$toast.loading({
        message: '加载中...',
        forbidClick: true, // 禁用背景点击
        duration: 0 // 持续时间，如果为0持续展示
      })
      // 3.提交表单申请登录
      try {
        const { data: res } = await login(user)
        this.$store.commit('setUser', res.data)
        // console.log(res.data)
        console.log(res.data)
        this.$toast.success('登陆成功')
        this.$router.replace(this.$route.query.path || '/my')
      } catch (err) {
        if (err.response.status === 400) {
          this.$toast.fail('手机号或验证码错误')
        } else {
          this.$toast.fail('登录失败，稍后重试')
        }
      }
      // 4.根据请求响应处理结果
    },
    async onSendSms() {
      // 校验手机号
      try {
        await this.$refs.loginForm.validate('mobile')
      } catch (err) {
        return this.$toast(err.message)
      }
      // 验证通过显示倒计时
      this.isCountDownShow = true
      // 请求发送验证码
      try {
        await sendSms(this.user.mobile)
        this.$toast('发送成功')
      } catch (err) {
        this.isCountDownShow = false
        if (err.response.status === 429) {
          this.$toast('发送过于频繁，请稍后再试')
        } else {
          this.$toast('发送失败，稍后重试')
        }
      }
    }
  }
}
</script>

<style lang="less" scoped>
.toutiao {
  font-size: 37px;
  margin-right: 10px;
}
.button-sub {
  margin: 0 !important;
  padding: 53px 33px;
  .login-btn {
    background-color: #6db4fb;
    border-radius: 10px;
    border: none;
  }
}
.send-sms-btn {
  width: 152px;
  height: 46px;
  background-color: #ededed;
  line-height: 46px;
  padding: 0;
  text-align: center;
  color: #666;
  border-radius: 23px;
  .van-button__text {
    font-size: 20px;
    color: #666;
  }
}
</style>
